:root {
  --brand: #41CBBF;
  --dark: #164E63;
  --light: #BEC9DF;
  --emerald: #46917a;
  --darkest: #0A3342;
  --primary: #8EC1D6;
}
.main__container--loading {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #0A3342;
  background-image: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.15) 0%,
    rgba(0, 153, 212, 0) calc(15% + 100px),
    rgba(0, 99, 138, 0) calc(85% + 100px),
    rgba(0, 0, 0, 0.15) 100%
  );
}
div.container__loading {
  display: flex;
  justify-content: center;
  align-items: center;
}
.container__loading > div {
  width: 3vw;
  height: 3vw;
  border-radius: 100%;
  margin: 2vw;
  background-image: linear-gradient(
    145deg,
    rgba(255, 255, 255, 0.5) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  animation: bounce 1.5s 0.5s linear infinite;
}
.green__dot {
  background-color: var(--brand);
}

.yellow__dot {
  background-color: var(--light);
  animation-delay: 0.1s;
}

.red__dot {
  background-color: var(--dark);
  animation-delay: 0.2s;
}

.emerald__dot {
  background-color: var(--emerald);
  animation-delay: 0.3s;
}

@keyframes bounce {
  0%,
  50%,
  100% {
    transform: scale(1);
    filter: blur(0px);
  }
  25% {
    transform: scale(0.6);
    filter: blur(3px);
  }
  75% {
    filter: blur(3px);
    transform: scale(1.4);
  }
}
